import axios from 'axios'

export default {
  namespaced: true,
  state: {
    countries: []
  },
  getters: {
    getCountries: s => s.countries
  },
  mutations: {
    setCountries: (s, value) => s.countries = value,
  },
  actions: {
    async apiCountries({
                         commit
                       }) {
      await axios({
        url: 'platform/countries',
        method: 'GET'
      }).then(response => {
        commit('setCountries', response.data.data)
      }).catch(() => {})
    },
  }
}



