import axios from 'axios'

export default {
  namespaced: true,
  state: {
    cities: [],
  },
  getters: {
    getCities: s => s.cities,
  },
  mutations: {
    setCities: (s, value) => s.cities = value,
  },
  actions: {
    async apiCities({
                         commit
                       }, countryId) {
      await axios({
        url: 'platform/cities?countryId=' + countryId,
        method: 'GET'
      }).then(response => {
        commit('setCities', response.data.data)
      }).catch(() => {})
    },
  }
}



